.cs-1 {
  .getColorSetDefaults(cs1);
  .getColorSetSections(cs1);
  .getColorSetLists(cs1);
  .getColorSetButtonMi24(cs1)
}

.cs-2 {
  .getColorSetDefaults(cs2);
  .getColorSetSections(cs2);
  .getColorSetLists(cs2);
  .getColorSetButtonMi24(cs2)
}

.cs-3 {
  .getColorSetDefaults(cs3);
  .getColorSetSections(cs3);
  .getColorSetLists(cs3);
  .getColorSetButtonMi24(cs3)
}

.getColorSetAsides();

header {
  &.cs-1 {
    .getColorSetHeader(cs1);
  }
  &.cs-2 {
    .getColorSetHeader(cs2);
  }
  &.cs-3 {
    .getColorSetHeader(cs3);
  }
}

